import { useState, useEffect } from 'react'
import { Navs } from './componentes/navs'
import { Inicio } from './componentes/inicio'
import { Historia } from './componentes/historia'
import { Eventos } from './componentes/eventos'
import { Contato } from './componentes/contato'
import JsonData from './data/data.json'
import SmoothScroll from 'smooth-scroll'

export const scroll = new SmoothScroll('a[href*="#"]', {
  speed: 1000,
  speedAsDuration: true,
})

const App = () => {
  const [jsonInformacoes, setJsonInformacoes] = useState({})
  useEffect(() => {
    setJsonInformacoes(JsonData)
  }, [])

  return (
    <div> 
      <Navs />
      <Inicio data={jsonInformacoes.Inicio} />
      <Historia data={jsonInformacoes.Historia} />
      <Eventos data={jsonInformacoes.Eventos} />
      <Contato data={jsonInformacoes.Contato} />
    </div>
  )
}

export default App